/*=============================
//  button
==============================*/

// Button 
.button {
  font-size: 16px;
  font-weight: 500;
  line-height: normal;
  border-radius: 6px;
}

// Primnary Button

.button-primary {
    background-color: $color-primary;
    color: $color-white;
    @include transition;

    &::before, &::after {
      background-color: $color-secondary !important;
    }

    &:hover {
      color: $color-white;
    }
}

// Secondary Button

.button-secondary {
    background-color: $color-secondary;
    color: $color-white;
    @include transition;
    &:hover {

    }
}

// Transparent Button

.button-transparent {
  background: #E6F0F1;
  color: $color-primary;
  @include transition;

  &:hover {
    color: $color-white;
    svg {
      fill: $color-white;
      @include transition;
    }
  }
}

// White Button
.button-white {
    gap: 10px;
    background: $color-white;
    color: $color-primary;
    position: relative;
    overflow: hidden;
    z-index: 1;
    svg {
        transition: all 0.4s ease;
    }
}

//   -------------

//btn-animation-1

.btn-animation {
  position: relative;
  overflow: hidden;
  transition: all .35s;
  z-index: 0;
  &::before, &::after {
    position: absolute;
    content: "";
    width: 100%;
    height: 100%;
    top: -100%;
    left: 0;
    background-color: $color-primary;
    z-index: -1;
    transition: all .35s;
  }
  &::before {
    opacity: .5;
  }
  
  &::after {
    transition-delay: .2s;
  }
  &:hover {
    color: #fff;
    &::before, &::after{
      top: 0;
    }
  }
}



// btn-animation-2
.e-button {
  display: inline-block;
  position: relative;
  padding: 10px;
  overflow: hidden;
   
  &__link {
    text-decoration: none;
    color: #fff;
    font-family: Helvetica;
    font-size: 30px;
    text-transform: uppercase;
    display: block; 
    text-align: center;
    position: relative;  
    padding: 10px 5px;
    border: 3px solid #fff;
  }
  
  .line {
    position: absolute;
    background-color: #fff;
    
    &--one {
      width: 1px;
      height: 100%; 
      left: 0;
      top: 0;
      transform: translate3d(0px, 102%, 0px);
    }
    
    &--two {
      width: 100%;
      height: 1px;
      left: 0;
      top: 0;
      transform: translate3d(-102%, 0px, 0px);

    }
    
    &--three {
      width: 1px;
      height: 100%;
      right: 0;
      top: 0;
      transform: translate3d(0px,-102%,0px);

    }
    &--four {
      width: 100%;
      height: 1px;
      left: 0;
      bottom: 0;
      transform: translate3d(102%,0px,0px);

    }
  }
  
  &:hover {
    .line {
      opacity: 1;
      &--one, &--two ,&--three, &--four {
        transform: translate3d(0, 0, 0);
        transition: transform .3s ease-in-out;
      }
      &--one {
        transition-delay: 0s;
      }
      &--two {
        transition-delay: 250ms;
      }
      &--three {
        transition-delay: 500ms;
      }
      &--four {
        transition-delay: 750ms;
      }
    }
  }
}

