.coolBeans {
  position: relative;
  overflow: hidden;
  z-index: 0;
  &::before {
    content: "";
    background-color: $color-black;
    height: 100%;
    left: -100%;
    top: 0;
    width: 0;
    bottom: 0;
    position: absolute;
    z-index: -1;
    transform: skew(50deg);
    transition: width 0.6s;
    transform-origin: top left;
    transition: all 0.5s ease;
  }
  &:hover {
    color: $color-white;
    &::before {
      height: 100%;
      width: 200%;
    }
  }
}

